import React from 'react';
import PlayerPropListData from "../data/PlayerPropListData";
import { Button, ButtonGroup, ListItem, ListItemIcon, ListItemText, Paper } from '@mui/material';

interface Props {
    id : number;
    collapsedData : PlayerPropListData;
    state : 'COLLAPSED' | 'EXPANDED';
}

const PropBetListItem: React.FC<Props> = (props) => {
    if (props.state === 'COLLAPSED') {
        return (
            <div>
              <ListItem key={props.id} alignItems='flex-start'>
                  <ListItemText primary={props.collapsedData.name + ' - ' + props.collapsedData.type}></ListItemText>
                <div style="margin-right: 0">
                    <ListItem primary={props.collapsedData.line}></ListItem>
                    <ButtonGroup>
                        <Button>Over </Button>
                        <Button>Under </Button>
                    </ButtonGroup>
                </div>
]
              </ListItem>
            </div>
          );
    } else {
        return ( <div> </div> );
    }

};

export default PropBetListItem;
  
  